:root {
  // Note: Custom variable values only support SassScript inside `#{}`.

  // Colors
  //
  // Generate palettes for full colors, grays, and theme colors.

  @each Rpcolor, Rpvalue in Rpcolors {
    --#{Rpprefix}#{Rpcolor}: #{Rpvalue};
  }

  @each Rpcolor, Rpvalue in Rpgrays {
    --#{Rpprefix}gray-#{Rpcolor}: #{Rpvalue};
  }

  @each Rpcolor, Rpvalue in Rptheme-colors {
    --#{Rpprefix}#{Rpcolor}: #{Rpvalue};
  }

  @each Rpcolor, Rpvalue in Rptheme-colors-rgb {
    --#{Rpprefix}#{Rpcolor}-rgb: #{Rpvalue};
  }

  --#{Rpprefix}white-rgb: #{to-rgb(Rpwhite)};
  --#{Rpprefix}black-rgb: #{to-rgb(Rpblack)};
  --#{Rpprefix}body-color-rgb: #{to-rgb(Rpbody-color)};
  --#{Rpprefix}body-bg-rgb: #{to-rgb(Rpbody-bg)};

  // Fonts

  // Note: Use `inspect` for lists so that quoted items keep the quotes.
  // See https://github.com/sass/sass/issues/2383#issuecomment-336349172
  --#{Rpprefix}font-sans-serif: #{inspect(Rpfont-family-sans-serif)};
  --#{Rpprefix}font-monospace: #{inspect(Rpfont-family-monospace)};
  --#{Rpprefix}gradient: #{Rpgradient};

  // Root and body
  // scss-docs-start root-body-variables
  @if Rpfont-size-root != null {
    --#{Rpprefix}root-font-size: #{Rpfont-size-root};
  }
  --#{Rpprefix}body-font-family: #{Rpfont-family-base};
  @include rfs(Rpfont-size-base, --#{Rpprefix}body-font-size);
  --#{Rpprefix}body-font-weight: #{Rpfont-weight-base};
  --#{Rpprefix}body-line-height: #{Rpline-height-base};
  --#{Rpprefix}body-color: #{Rpbody-color};
  @if Rpbody-text-align != null {
    --#{Rpprefix}body-text-align: #{Rpbody-text-align};
  }
  --#{Rpprefix}body-bg: #{Rpbody-bg};
  // scss-docs-end root-body-variables

  // scss-docs-start root-border-var
  --#{Rpprefix}border-width: #{Rpborder-width};
  --#{Rpprefix}border-style: #{Rpborder-style};
  --#{Rpprefix}border-color: #{Rpborder-color};
  --#{Rpprefix}border-color-translucent: #{Rpborder-color-translucent};

  --#{Rpprefix}border-radius: #{Rpborder-radius};
  --#{Rpprefix}border-radius-sm: #{Rpborder-radius-sm};
  --#{Rpprefix}border-radius-lg: #{Rpborder-radius-lg};
  --#{Rpprefix}border-radius-xl: #{Rpborder-radius-xl};
  --#{Rpprefix}border-radius-2xl: #{Rpborder-radius-2xl};
  --#{Rpprefix}border-radius-pill: #{Rpborder-radius-pill};
  // scss-docs-end root-border-var

  --#{Rpprefix}link-color: #{Rplink-color};
  --#{Rpprefix}link-hover-color: #{Rplink-hover-color};

  --#{Rpprefix}code-color: #{Rpcode-color};

  --#{Rpprefix}highlight-bg: #{Rpmark-bg};
}
