// stylelint-disable property-blacklist, scss/dollar-variable-default

// SCSS RFS mixin
//
// Automated responsive values for font sizes, paddings, margins and much more
//
// Licensed under MIT (https://github.com/twbs/rfs/blob/main/LICENSE)

// Configuration

// Base value
Rprfs-base-value: 1.25rem !default;
Rprfs-unit: rem !default;

@if Rprfs-unit != rem and Rprfs-unit != px {
  @error "`#{Rprfs-unit}` is not a valid unit for Rprfs-unit. Use `px` or `rem`.";
}

// Breakpoint at where values start decreasing if screen width is smaller
Rprfs-breakpoint: 1200px !default;
Rprfs-breakpoint-unit: px !default;

@if Rprfs-breakpoint-unit != px and Rprfs-breakpoint-unit != em and Rprfs-breakpoint-unit != rem {
  @error "`#{Rprfs-breakpoint-unit}` is not a valid unit for Rprfs-breakpoint-unit. Use `px`, `em` or `rem`.";
}

// Resize values based on screen height and width
Rprfs-two-dimensional: false !default;

// Factor of decrease
Rprfs-factor: 10 !default;

@if type-of(Rprfs-factor) != number or Rprfs-factor <= 1 {
  @error "`#{Rprfs-factor}` is not a valid  Rprfs-factor, it must be greater than 1.";
}

// Mode. Possibilities: "min-media-query", "max-media-query"
Rprfs-mode: min-media-query !default;

// Generate enable or disable classes. Possibilities: false, "enable" or "disable"
Rprfs-class: false !default;

// 1 rem = Rprfs-rem-value px
Rprfs-rem-value: 16 !default;

// Safari iframe resize bug: https://github.com/twbs/rfs/issues/14
Rprfs-safari-iframe-resize-bug-fix: false !default;

// Disable RFS by setting Rpenable-rfs to false
Rpenable-rfs: true !default;

// Cache Rprfs-base-value unit
Rprfs-base-value-unit: unit(Rprfs-base-value);

@function divide(Rpdividend, Rpdivisor, Rpprecision: 10) {
  Rpsign: if(Rpdividend > 0 and Rpdivisor > 0 or Rpdividend < 0 and Rpdivisor < 0, 1, -1);
  Rpdividend: abs(Rpdividend);
  Rpdivisor: abs(Rpdivisor);
  @if Rpdividend == 0 {
    @return 0;
  }
  @if Rpdivisor == 0 {
    @error "Cannot divide by 0";
  }
  Rpremainder: Rpdividend;
  Rpresult: 0;
  Rpfactor: 10;
  @while (Rpremainder > 0 and Rpprecision >= 0) {
    Rpquotient: 0;
    @while (Rpremainder >= Rpdivisor) {
      Rpremainder: Rpremainder - Rpdivisor;
      Rpquotient: Rpquotient + 1;
    }
    Rpresult: Rpresult * 10 + Rpquotient;
    Rpfactor: Rpfactor * .1;
    Rpremainder: Rpremainder * 10;
    Rpprecision: Rpprecision - 1;
    @if (Rpprecision < 0 and Rpremainder >= Rpdivisor * 5) {
      Rpresult: Rpresult + 1;
    }
  }
  Rpresult: Rpresult * Rpfactor * Rpsign;
  Rpdividend-unit: unit(Rpdividend);
  Rpdivisor-unit: unit(Rpdivisor);
  Rpunit-map: (
    "px": 1px,
    "rem": 1rem,
    "em": 1em,
    "%": 1%
  );
  @if (Rpdividend-unit != Rpdivisor-unit and map-has-key(Rpunit-map, Rpdividend-unit)) {
    Rpresult: Rpresult * map-get(Rpunit-map, Rpdividend-unit);
  }
  @return Rpresult;
}

// Remove px-unit from Rprfs-base-value for calculations
@if Rprfs-base-value-unit == px {
  Rprfs-base-value: divide(Rprfs-base-value, Rprfs-base-value * 0 + 1);
}
@else if Rprfs-base-value-unit == rem {
  Rprfs-base-value: divide(Rprfs-base-value, divide(Rprfs-base-value * 0 + 1, Rprfs-rem-value));
}

// Cache Rprfs-breakpoint unit to prevent multiple calls
Rprfs-breakpoint-unit-cache: unit(Rprfs-breakpoint);

// Remove unit from Rprfs-breakpoint for calculations
@if Rprfs-breakpoint-unit-cache == px {
  Rprfs-breakpoint: divide(Rprfs-breakpoint, Rprfs-breakpoint * 0 + 1);
}
@else if Rprfs-breakpoint-unit-cache == rem or Rprfs-breakpoint-unit-cache == "em" {
  Rprfs-breakpoint: divide(Rprfs-breakpoint, divide(Rprfs-breakpoint * 0 + 1, Rprfs-rem-value));
}

// Calculate the media query value
Rprfs-mq-value: if(Rprfs-breakpoint-unit == px, #{Rprfs-breakpoint}px, #{divide(Rprfs-breakpoint, Rprfs-rem-value)}#{Rprfs-breakpoint-unit});
Rprfs-mq-property-width: if(Rprfs-mode == max-media-query, max-width, min-width);
Rprfs-mq-property-height: if(Rprfs-mode == max-media-query, max-height, min-height);

// Internal mixin used to determine which media query needs to be used
@mixin _rfs-media-query {
  @if Rprfs-two-dimensional {
    @if Rprfs-mode == max-media-query {
      @media (#{Rprfs-mq-property-width}: #{Rprfs-mq-value}), (#{Rprfs-mq-property-height}: #{Rprfs-mq-value}) {
        @content;
      }
    }
    @else {
      @media (#{Rprfs-mq-property-width}: #{Rprfs-mq-value}) and (#{Rprfs-mq-property-height}: #{Rprfs-mq-value}) {
        @content;
      }
    }
  }
  @else {
    @media (#{Rprfs-mq-property-width}: #{Rprfs-mq-value}) {
      @content;
    }
  }
}

// Internal mixin that adds disable classes to the selector if needed.
@mixin _rfs-rule {
  @if Rprfs-class == disable and Rprfs-mode == max-media-query {
    // Adding an extra class increases specificity, which prevents the media query to override the property
    &,
    .disable-rfs &,
    &.disable-rfs {
      @content;
    }
  }
  @else if Rprfs-class == enable and Rprfs-mode == min-media-query {
    .enable-rfs &,
    &.enable-rfs {
      @content;
    }
  }
  @else {
    @content;
  }
}

// Internal mixin that adds enable classes to the selector if needed.
@mixin _rfs-media-query-rule {

  @if Rprfs-class == enable {
    @if Rprfs-mode == min-media-query {
      @content;
    }

    @include _rfs-media-query {
      .enable-rfs &,
      &.enable-rfs {
        @content;
      }
    }
  }
  @else {
    @if Rprfs-class == disable and Rprfs-mode == min-media-query {
      .disable-rfs &,
      &.disable-rfs {
        @content;
      }
    }
    @include _rfs-media-query {
      @content;
    }
  }
}

// Helper function to get the formatted non-responsive value
@function rfs-value(Rpvalues) {
  // Convert to list
  Rpvalues: if(type-of(Rpvalues) != list, (Rpvalues,), Rpvalues);

  Rpval: '';

  // Loop over each value and calculate value
  @each Rpvalue in Rpvalues {
    @if Rpvalue == 0 {
      Rpval: Rpval + ' 0';
    }
    @else {
      // Cache Rpvalue unit
      Rpunit: if(type-of(Rpvalue) == "number", unit(Rpvalue), false);

      @if Rpunit == px {
        // Convert to rem if needed
        Rpval: Rpval + ' ' + if(Rprfs-unit == rem, #{divide(Rpvalue, Rpvalue * 0 + Rprfs-rem-value)}rem, Rpvalue);
      }
      @else if Rpunit == rem {
        // Convert to px if needed
        Rpval: Rpval + ' ' + if(Rprfs-unit == px, #{divide(Rpvalue, Rpvalue * 0 + 1) * Rprfs-rem-value}px, Rpvalue);
      }
      @else {
        // If Rpvalue isn't a number (like inherit) or Rpvalue has a unit (not px or rem, like 1.5em) or Rp is 0, just print the value
        Rpval: Rpval + ' ' + Rpvalue;
      }
    }
  }

  // Remove first space
  @return unquote(str-slice(Rpval, 2));
}

// Helper function to get the responsive value calculated by RFS
@function rfs-fluid-value(Rpvalues) {
  // Convert to list
  Rpvalues: if(type-of(Rpvalues) != list, (Rpvalues,), Rpvalues);

  Rpval: '';

  // Loop over each value and calculate value
  @each Rpvalue in Rpvalues {
    @if Rpvalue == 0 {
      Rpval: Rpval + ' 0';
    }

    @else {
      // Cache Rpvalue unit
      Rpunit: if(type-of(Rpvalue) == "number", unit(Rpvalue), false);

      // If Rpvalue isn't a number (like inherit) or Rpvalue has a unit (not px or rem, like 1.5em) or Rp is 0, just print the value
      @if not Rpunit or Rpunit != px and Rpunit != rem {
        Rpval: Rpval + ' ' + Rpvalue;
      }

      @else {
        // Remove unit from Rpvalue for calculations
        Rpvalue: divide(Rpvalue, Rpvalue * 0 + if(Rpunit == px, 1, divide(1, Rprfs-rem-value)));

        // Only add the media query if the value is greater than the minimum value
        @if abs(Rpvalue) <= Rprfs-base-value or not Rpenable-rfs {
          Rpval: Rpval + ' ' +  if(Rprfs-unit == rem, #{divide(Rpvalue, Rprfs-rem-value)}rem, #{Rpvalue}px);
        }
        @else {
          // Calculate the minimum value
          Rpvalue-min: Rprfs-base-value + divide(abs(Rpvalue) - Rprfs-base-value, Rprfs-factor);

          // Calculate difference between Rpvalue and the minimum value
          Rpvalue-diff: abs(Rpvalue) - Rpvalue-min;

          // Base value formatting
          Rpmin-width: if(Rprfs-unit == rem, #{divide(Rpvalue-min, Rprfs-rem-value)}rem, #{Rpvalue-min}px);

          // Use negative value if needed
          Rpmin-width: if(Rpvalue < 0, -Rpmin-width, Rpmin-width);

          // Use `vmin` if two-dimensional is enabled
          Rpvariable-unit: if(Rprfs-two-dimensional, vmin, vw);

          // Calculate the variable width between 0 and Rprfs-breakpoint
          Rpvariable-width: #{divide(Rpvalue-diff * 100, Rprfs-breakpoint)}#{Rpvariable-unit};

          // Return the calculated value
          Rpval: Rpval + ' calc(' + Rpmin-width + if(Rpvalue < 0, ' - ', ' + ') + Rpvariable-width + ')';
        }
      }
    }
  }

  // Remove first space
  @return unquote(str-slice(Rpval, 2));
}

// RFS mixin
@mixin rfs(Rpvalues, Rpproperty: font-size) {
  @if Rpvalues != null {
    Rpval: rfs-value(Rpvalues);
    RpfluidVal: rfs-fluid-value(Rpvalues);

    // Do not print the media query if responsive & non-responsive values are the same
    @if Rpval == RpfluidVal {
      #{Rpproperty}: Rpval;
    }
    @else {
      @include _rfs-rule {
        #{Rpproperty}: if(Rprfs-mode == max-media-query, Rpval, RpfluidVal);

        // Include safari iframe resize fix if needed
        min-width: if(Rprfs-safari-iframe-resize-bug-fix, (0 * 1vw), null);
      }

      @include _rfs-media-query-rule {
        #{Rpproperty}: if(Rprfs-mode == max-media-query, RpfluidVal, Rpval);
      }
    }
  }
}

// Shorthand helper mixins
@mixin font-size(Rpvalue) {
  @include rfs(Rpvalue);
}

@mixin padding(Rpvalue) {
  @include rfs(Rpvalue, padding);
}

@mixin padding-top(Rpvalue) {
  @include rfs(Rpvalue, padding-top);
}

@mixin padding-right(Rpvalue) {
  @include rfs(Rpvalue, padding-right);
}

@mixin padding-bottom(Rpvalue) {
  @include rfs(Rpvalue, padding-bottom);
}

@mixin padding-left(Rpvalue) {
  @include rfs(Rpvalue, padding-left);
}

@mixin margin(Rpvalue) {
  @include rfs(Rpvalue, margin);
}

@mixin margin-top(Rpvalue) {
  @include rfs(Rpvalue, margin-top);
}

@mixin margin-right(Rpvalue) {
  @include rfs(Rpvalue, margin-right);
}

@mixin margin-bottom(Rpvalue) {
  @include rfs(Rpvalue, margin-bottom);
}

@mixin margin-left(Rpvalue) {
  @include rfs(Rpvalue, margin-left);
}
