// scss-docs-start spacer-variables-maps
Rpspacer: 1rem !default;
Rpspacers: (
  0: 0,
  1: Rpspacer * .25,
  2: Rpspacer * .5,
  3: Rpspacer,
  4: Rpspacer * 1.5,
  5: Rpspacer * 3,
) !default;
// scss-docs-end spacer-variables-maps

