Rplink-hover-decoration:                   null !default;

Rpstretched-link-pseudo-element:           after !default;
Rpstretched-link-z-index:                  1 !default;

// Paragraphs
//
// Style p element.

Rpparagraph-margin-bottom:   1rem !default;


// Grid breakpoints
//
// Define the minimum dimensions at which your layout will change,
// adapting to different screen sizes, for use in media queries.

// scss-docs-start grid-breakpoints
Rpgrid-breakpoints: (
