  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px
) !default;
// scss-docs-end grid-breakpoints

@include _assert-ascending(Rpgrid-breakpoints, "Rpgrid-breakpoints");
@include _assert-starts-at-zero(Rpgrid-breakpoints, "Rpgrid-breakpoints");


